import React,{useState} from "react";
import styled from "styled-components";
import Color from "../colors/colors.js"


const OverlayWrapper = function ({name,details, className}) {
    return (
        <div className={className + " overlay"}>
            <h1>{name}</h1>
            <p>{details}</p>
        </div>
    );
}

const Overlay = styled(OverlayWrapper)`
  display: flex;
  flex: 1 5 auto;
  border-radius: 5px;
  
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  
  color: white;
  background-color: rgba(0,0,0) ;
  transition: .6s;
  opacity: 0;
  
  &:hover{
  opacity: 0.6;
  cursor: pointer;
  
  }
`;


const ModalWrapper = function({className,onClick}){
    return(
        <div className={className + " wrapper"}>

        </div>
    );
}

const Modal = styled(ModalWrapper)`
  width:100%;
  height:100%;
  background-color: rgba(0,0,0);
  opacity: 0.6;
  position: fixed;
  top:0;
  left:0;
  z-index: 100;
  
 
`;



const Wrapper = function (props) {


    return (

        <div onClick={props.onClick}  className={props.className}>
            <Overlay name={props.name} details={props.description}/>
        </div>
    );
}

const Project = styled(Wrapper)`
  flex: 1 5 auto;
  border-radius: 5px;
  box-shadow: 5px 5px 15px 0  #cacbcd;
  margin: 1rem;  
  position: relative;
  background: url(${props => props.pictures}) no-repeat;
  background-size: cover;
  width: 20rem;
  height: 20rem;
  &:hover{
  backdrop-filter: blur(4px);
  }
`;

export default Project;