import React,{useState,useEffect} from "react";
import styled from "styled-components";
import Project from "../Project/Project";
import test_cover from "../assets/Design Projects/Green Whisk/Tin Container Packaging MockUp 2.png"
import test_picture from "../assets/Design Projects/Green Whisk/identity-design-mockup-01-scene-02.png"
import {BrowserRouter as Router, NavLink, Route, Switch, useLocation} from "react-router-dom";



const Container = function (props){
     const [data,setData] = useState([]);
     const [tilesVisible,setTilesVisible] = useState(true);
     const location = useLocation();

    
    useEffect( ()=>{
         let fetchedData = data;
             (async ()=>{
                 let res = await (await fetch("/myroute")).json();
                 console.log(res);


                 if(fetchedData.length === 0){
                 fetchedData =await (await fetch("https://jsonplaceholder.typicode.com/posts"))
                     .json();
                 }
                 setData(fetchedData);
             })();

     },[]);

    useEffect(()=>{
        setTilesVisible(location.pathname === "/projects");

    },[location])


     function handleTileClick(evt){

         setTilesVisible(false);
     }

    return (
        <div  className={props.className}>
        <Router>

            { tilesVisible &&
            data.map(post =>{
                return(
                    <NavLink key={`${post.id}${Math.random()}`} to={`/projects/${post.id}`}>
                        <Project onClick={handleTileClick} name={post.title} description={post.body} cover={test_cover} pictures={test_picture}/>
                    </NavLink>
                );
            }).slice(0,9)}

            <Switch>
                {
                    data.map(post =>{
                        return(
                            <Route key={`${post.id}${Math.random()}${String.fromCharCode(Math.random() * 100)}`}
                                   exact path={`/projects/${post.id}`}>
                                <h1>Project {post.title}</h1>
                            </Route>
                        );
                    }).slice(0,9)
                }

            </Switch>

        </Router>
        </div>
    );
}

const Projects = styled(Container)`
  
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
 
  
`;

export default Projects;